/* eslint-disable @typescript-eslint/naming-convention */
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';

@Component({
  selector: 'app-favourites',
  templateUrl: './favourites.page.html',
  styleUrls: ['./favourites.page.scss'],
})
export class FavouritesPage implements OnInit {
  loggedUser: any;
  favourites: any = [];
  pasts: any = [];
  segment: any = 'favourite';
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router
  ) {
    this.loggedUser = this.localService.getUser();
    this.getData();
  }

  ngOnInit() {}

  remove(cleanerId) {
    // unfavourite
    this.restService
      .postData('favourite/delete', {
        cleaner_id: cleanerId,
        user_id: this.loggedUser.id,
      })
      .subscribe(
        (data) => {
          // update favourites
          this.getData();
        },
        (err) => {}
      );
  }

  add(cleanerId) {
    // unfavourite
    this.restService
      .postData('favourite/create', {
        cleaner_id: cleanerId,
        user_id: this.loggedUser.id,
      })
      .subscribe(
        (data) => {
          // update favourites
          this.getData();
        },
        (err) => {}
      );
  }

  getData() {
    this.utilityService.loadingOpen('Loading...');
    this.restService.getData('favourite/get/' + this.loggedUser.id).subscribe(
      (data) => {
        this.favourites = data.favourite;
        this.pasts = data.past;
        this.utilityService.loadingClose();
      },
      (err) => {
        this.utilityService.loadingClose();
      }
    );
  }

  cleaner(id) {
    this.router.navigate(['/cleanerprofile'], {
      queryParams: {
        id: id
      }
    });
  }
}
